import React from 'react';
import { NextPage } from 'next';
import ProjectMarkdown from '../../src/ProjectMarkdown';
import { getMarkdown } from '../../util/projectData';


const ProjectPage: NextPage<string> = (content) => {
  console.log("rendering ");
  return (<ProjectMarkdown projectMD={content} />)
}

ProjectPage.getInitialProps = async ({ query }) => {
  console.log("GetInitialProps for [pid].tsx with query: ", query);
  const content = getMarkdown(query.pid as string);
  console.log("got the promise")
  return content;
}

export default ProjectPage;